export enum TileShowcaseItemExtendedAlignment {
  LEFT,
  RIGHT,
}

export interface TileShowcaseItem {
  readonly id?: string;
  readonly previewWidth?: number;
  readonly previewHeight?: number;
}

export interface TileShowcaseItemExtended extends TileShowcaseItem {
  readonly alignment?: TileShowcaseItemExtendedAlignment;
}

export enum TileShowcaseCategoryType {
  COVER = 'COVER',
  SPECIAL_OFFER = 'SPECIAL_OFFER',
  COMPANY_INFO = 'COMPANY_INFO',
  SHIPPING_PAYMENT = 'SHIPPING_PAYMENT',
  STORE = 'STORE',
  LOCATION = 'LOCATION'
}

export interface TileShowcaseCategory {
  readonly type?: TileShowcaseCategoryType;
  readonly items?: TileShowcaseItem[];
}
