






















































































import {
  computed, defineComponent, onMounted, Ref, ref,
} from '@vue/composition-api';
import { Store } from 'vuex';
import { State } from '@/app/editor/client/store/state';
import { ActionType } from '@/app/editor/client/store/action/type';
import { SetPopupMutation } from '@/app/editor/client/store/mutation/set-popup';
import { MutationType } from '@/app/editor/client/store/mutation/type';
import { VueRouter } from 'vue-router/types/router';
import {
  GetTileShowcaseDataAction,
} from '@/app/editor/client/store/action/get-tile-showcase-data';
import useI18N from '@/app/shared/client/logic/i18n';
import {
  TileShowcaseCategoryType, TileShowcaseItem,
  TileShowcaseItemExtended,
  TileShowcaseItemExtendedAlignment,
} from '@/app/editor/client/type/showcase';
import { AddTileFromShowcaseAction } from '@/app/editor/client/store/action/add-tile-from-showcase';
import { TileData } from '@/app/shared/client/type/tile';

function useActiveCategoryUpdater(
  bodyRef: Ref<HTMLElement | undefined>,
  skipScrollEventTimer: Ref<number | undefined>,
  selectedCategoryIndex: Ref<number>,
): () => void {
  function updateActiveCategoryIndex(): void {
    if (bodyRef.value === undefined || skipScrollEventTimer.value !== undefined) {
      return;
    }

    const categoryElements = bodyRef.value.children;
    const screenCenter = bodyRef.value.scrollTop + bodyRef.value.offsetHeight * 0.4;
    for (let i = categoryElements.length - 1; i >= 0; i -= 1) {
      if ((categoryElements[i] as HTMLElement).offsetTop < screenCenter) {
        selectedCategoryIndex.value = i;
        return;
      }
    }
  }

  return updateActiveCategoryIndex;
}

function useScrollHandler(
  bodyRef: Ref<HTMLElement | undefined>,
  skipScrollEventTimer: Ref<number | undefined>,
  selectedCategoryIndex: Ref<number>,
): () => void {
  const updateActiveCategoryIndex = useActiveCategoryUpdater(
    bodyRef,
    skipScrollEventTimer,
    selectedCategoryIndex,
  );

  function subscribeOnScrollChange(): void {
    if (bodyRef.value === undefined) {
      return;
    }
    bodyRef.value.addEventListener('scroll', updateActiveCategoryIndex);
    updateActiveCategoryIndex();
  }

  return subscribeOnScrollChange;
}

function useSelectCategoryHandler(
  bodyRef: Ref<HTMLElement | undefined>,
  skipScrollEventTimer: Ref<number | undefined>,
  selectedCategoryIndex: Ref<number>,
): (value: number) => void {
  function selectCategory(newIndex: number): void {
    selectedCategoryIndex.value = newIndex;
    if (bodyRef.value !== undefined) {
      if (skipScrollEventTimer.value !== undefined) {
        window.clearInterval(skipScrollEventTimer.value);
      }
      skipScrollEventTimer.value = window.setTimeout(() => {
        skipScrollEventTimer.value = undefined;
      }, 800);

      const { offsetTop } = (bodyRef.value.children[newIndex] as HTMLElement);
      bodyRef.value.scrollTo({
        top: offsetTop,
        behavior: 'smooth',
      });
    }
  }
  return selectCategory;
}

function useNavigate(router: VueRouter): (tileId: string) => void {
  function navigate(tileId: string): void {
    router.push({ path: `/edit/${tileId}` });
  }
  return navigate;
}

function useAddTileHandler(
  store: Store<State>,
  navigate: (tileId: string) => void,
): (tileShowcaseItemId: string) => void {
  function addTile(
    tileShowcaseItemId: string,
  ): void {
    store.dispatch<AddTileFromShowcaseAction>({
      type: ActionType.ADD_TILE_FROM_SHOWCASE,
      payload: { tileShowcaseItemId },
    }).then((tile: TileData) => {
      navigate(tile.id);
      store.commit<SetPopupMutation>({
        type: MutationType.SET_POPUP,
        payload: {
          popup: undefined,
        },
      });
    });
  }
  return addTile;
}

function getCategoryName(category: TileShowcaseCategoryType | undefined): string {
  switch (category) {
    case TileShowcaseCategoryType.COVER:
      return 'cover';
    case TileShowcaseCategoryType.SPECIAL_OFFER:
      return 'special_offer';
    case TileShowcaseCategoryType.COMPANY_INFO:
      return 'company_info';
    case TileShowcaseCategoryType.SHIPPING_PAYMENT:
      return 'shipping_payment';
    case TileShowcaseCategoryType.STORE:
      return 'store';
    case TileShowcaseCategoryType.LOCATION:
      return 'location';
    default:
      throw new Error('Unknown tile showcase item category');
  }
}

function getItemsList(
  itemsList: TileShowcaseItem[] | undefined,
): TileShowcaseItemExtended[] {
  const resultList: TileShowcaseItemExtended[] = [];
  let elementsHeightLeft = 0;
  let elementsHeightRight = 0;
  itemsList?.forEach((item: TileShowcaseItem) => {
    if (elementsHeightLeft > elementsHeightRight) {
      elementsHeightRight += item?.previewHeight ?? 0;
      resultList.push({
        ...item,
        alignment: TileShowcaseItemExtendedAlignment.RIGHT,
      });
    } else {
      elementsHeightLeft += item?.previewHeight ?? 0;
      resultList.push({
        ...item,
        alignment: TileShowcaseItemExtendedAlignment.LEFT,
      });
    }
  });
  return resultList;
}

const TileShowcasePopup = defineComponent({
  setup(_, { root }) {
    root.$store.dispatch<GetTileShowcaseDataAction>({
      type: ActionType.GET_TILE_SHOWCASE_DATA,
    });

    const i18n = useI18N(root.$store);
    const state = computed(() => root.$store.state as State);

    const categoriesWithItems = computed(() => {
      const categories = state.value.tileShowcaseCategories;
      return categories.map((category) => ({
        type: category.type,
        name: getCategoryName(category.type),
        items: getItemsList(category.items),
      }));
    });

    const bodyRef = ref<HTMLElement>();

    const skipScrollEventTimer = ref(undefined);
    const selectedCategoryIndex = ref(0);
    const subscribeOnScrollChange = useScrollHandler(
      bodyRef,
      skipScrollEventTimer,
      selectedCategoryIndex,
    );

    const selectCategory = useSelectCategoryHandler(
      bodyRef,
      skipScrollEventTimer,
      selectedCategoryIndex,
    );

    const navigate = useNavigate(root.$router);
    const addTile = useAddTileHandler(root.$store, navigate);

    onMounted(() => {
      subscribeOnScrollChange();
    });

    return {
      bodyRef,
      i18n,
      categoriesWithItems,
      selectedCategoryIndex,
      selectCategory,
      addTile,
      TileShowcaseItemExtendedAlignment,
    };
  },
});

export default TileShowcasePopup;
